.navbar {
    width: 85%;
    margin: auto;
    padding: 35px 0;
    display: flex;
    align-items: center;
    justify-content: space-between; 
}

.navbar ul li {
    list-style: none;
    display: inline-block;
    margin: 0 20px;
 }

.navbar ul li a{
    text-decoration: none;
    color: white;
    text-transform: uppercase;
}

.name {
    color: white;
}